
*{
	padding:0;
	margin:0;
}
html{
	height:100%;
}
body{
	height:100%;
}
#app{
	height:100%;
}
.index{
	display: inline-block;
	-webkit-transform:translate3d(-100vw,0,0);
	        transform:translate3d(-100vw,0,0);
	height:100%;
}
.main{
	overflow: hidden;
	white-space:nowrap;
	font-size:0;
	height:100%;
}
.card{
	font-size:0;
	width:auto;
	
	white-space:nowrap;
}
.search{
	width:90vw;
	display: inline-block;
	height:60px;
	background: #f3f3f3;
	margin-left:5vw;
	margin-top:10px;
	border-radius: 10px;
	text-align: center;
	margin-bottom:14px;
	position: relative;
}
.slider_f5{
	position: absolute;
	width: 100%;
	height: 50px;
	font-size: 34px;
	bottom: -70px;
	z-index: 2;
}
.all_card{
	position: relative;
	z-index: 10;
	background: #fff;
	overflow-y: scroll;
	height:80%;
}
.glass{
	height:38px;
	margin-top:11px;
	vertical-align: middle;
	margin-right:5px;
}
.search-tit{
	font-size:32px;
	color:#c4c4c4;
	vertical-align: middle;
	margin-top:12px;
	display: inline-block;
}
.list{
	display: inline-block;
	width:100vw;
	height:130px;
	padding:15px 5%;
	box-sizing: border-box;
	font-size:0;
	vertical-align: middle;
	border-bottom:solid 1px #e7e7e7;
}
.user-p{
	width:100px;
	vertical-align: middle;
	margin-right:22px;
}
.user-n{
	display: inline-block;
	font-size:20px;
	vertical-align: middle;
}
.user-name{
	font-size:34px;
	color:#000;
	margin-bottom:10px;
}
.user-content{
	font-size:24px;
	color:#7b7b7b;
}
.l-right{
	float:right;
	color:#b1b1b1;
	font-size:22px;
	margin-top:18px;
}
.l-circle{
	width:30px;
	height:30px;
	border-radius: 100%;
	background: #fb4c2f;
	color:#fff;
	text-align: center;
	line-height: 30px;
	margin-left:40px;
	margin-top:5px;
}
.slider{
	display: inline-block;
	height:130px;
	width:340px;
	color:#fff;
	vertical-align: middle;
	line-height: 130px;
	text-align: center;
}
.to-top{
	font-size:28px;
	background: #c8c7cf;
	width:170px;
	display: inline-block;
}
.delete{
	font-size:28px;
	display: inline-block;
	background: #ff3b32;
	width:170px;
}



